<template>
  <div class="freight-template">
      <!--面包导航-->
    <div class="nav-bar clear-fix">
      <el-breadcrumb separator=">" class="fl-l">
        <el-breadcrumb-item>运费模板管理</el-breadcrumb-item>
        <el-breadcrumb-item>{{addForm.template.id==''?'添加':'编辑'}}运费模板</el-breadcrumb-item>
      </el-breadcrumb>
      
    </div>
    <!-- 运费模块信息 -->
    <!-- 名称信息 -->
    <div class="info-base">
      <div class="item item-title clear-fix">
        <div class="item-label fl-l">模板信息</div>
        <div class="item-content" style="padding-top:12px;"><a href="http://s1.pic.intmall.net/file/vendors/freight-guide.pdf" target="_blank" class="tip"><i class="iconfont icon-guide"></i>运费模板设置操作指引</a></div>   
      </div>
      <div class="item  clear-fix">
        <div class="item-label fl-l"><i class="iconfont icon-required"></i> 模板名称：  </div>
        <div class="item-content">
           <el-input v-model="addForm.template.templateTitle" size="small"   ></el-input>
        </div>

      <div ref="ref_templateTitle" tabindex = "0">
        <div class="error"  v-show="errors.templateTitle">
            <i class="iconfont icon-error"></i>请填写模板标题
        </div>
      </div>
         
      </div>
      <div class="item  clear-fix">
         <div class="item-label fl-l"><i class="iconfont icon-required"></i>仓库名称:  </div>
         <div class="item-content">
           <el-select v-model="addForm.template.logisticsWarehouseCode" placeholder="请选择" size="small" clearable filterable>
              <el-option :value="item.code" :label="item.name" v-for="item in warehouseList" :key="item.code"></el-option>  
            </el-select>
         </div>
      </div>
      <div ref="ref_country" tabindex = "0">
        <div class="error"  v-show="errors.country">
            <i class="iconfont icon-error"></i>请选择发货仓库
        </div>
      </div>   
    </div>

    <!-- 设置信息 -->
    <div class="setting-box">
      <!-- 为物流方式添加组合 -->
      <div class="setting-add" v-if="type=='add'">
        <div class="info-setting ">
          <div class="logistic ">
            <div class="title">物流方式</div>
            <div class="list">
              <el-checkbox-group v-model="editForm.logisticsTypeCodeArr" size="small"  >
                <div :class="'item '+(item.logisticsCode==editForm.logisticsTypeCode?'select':'')" v-for="item in logisticsList_add" :key="item.logisticsCode" v-if="item.logisticsCode==editForm.logisticsTypeCode" >
                  <el-checkbox  :label="item.logisticsCode"  disabled> &nbsp;</el-checkbox> 
                  <div class="name-box">                  
                    <div class="cn">{{item.logisticsName}}{{item.logisticsCode==2400?'(尼日利亚流向必选)':''}}</div>
                  </div>  
                </div>
              </el-checkbox-group>
            </div>
          </div>
          <div class="setting" :style="{'min-height':logisticsList_add.length*35+30+'px'}">
            <div class="setting-wrap">
              <div class="zuhe-name">
                <span class="txt">分区及价格名称</span><el-input v-model="editForm.itemName" size="small" :maxlength="15" ></el-input>
                <a href="http://s1.pic.intmall.net/file/vendors/freight-guide-seko.pdf" v-if="editForm.logisticsTypeCode==2400" target="_blank">SEKO尼日尼亚发货指导</a>
              </div>
              <div class="sub-title">1.请按地区选择</div>
              <div class="content">
                
                <div class="setting-area" v-show="editForm.areaShowFlag">           
                  <div class="check-all">
                       <el-checkbox :indeterminate="checkAll.isIndeterminate" v-model="checkAll.checkAll" @change="handleCheckAllChange">全选</el-checkbox>
                  </div>
                  <div class="area-group" style="padding-left:15px;">                    
                    <el-checkbox-group v-model="editForm.areaSelectedId" @change="handleCheckedAreasChange">
                       <div class="list-box " v-for="(item,index) in areaList" :key="index" v-if="(editForm.logisticsTypeCode==1306&&item.id==60)||(editForm.logisticsTypeCode!=1306&&item.id!=60)">
                         <div class="item-title">{{item.areaName}}</div>
                         <div class="item">
                          <el-checkbox :label="item2.id" :disabled="item2.disabled" v-for="(item2,index2) in item.children" :key="index2">{{item2.areaName}}</el-checkbox>
                         </div>
                       </div>
                      
                    </el-checkbox-group>
                  </div>
                  
                  <div class="time-tip" style="color:#EA2000;"></div>
                </div>  

              </div>  

              <div class="sub-title">
                2.请设置运费类型
                <!-- editForm.logisticsTypeCode==2400 -->
                
              </div>
              <div class="freight-item">                
                 <el-form :model="editForm" label-width="130px" :inline="true" ref="weightForm">
                    <div>
                      <el-form-item label="运费类型：" :rules="{ required: true, message: '该字段必填' }" prop="freightType">
                          <el-radio-group v-model="editForm.freightType">
                              <el-radio :label="1">免运费</el-radio>
                              <el-radio :label="2">自定义运费</el-radio>
                          </el-radio-group>
                      </el-form-item> 
                      <span class="weight-tip" v-if="editForm.logisticsTypeCode==2400">运费计算参考：$15/KG，首重10g</span>                     
                    </div>
                    <div class="item-box" v-show="editForm.freightType==2">
                      <div>
                      <el-form-item label="首重" :rules="{ required: true, message: '该字段必填' }" prop="firstWeight_f">
                          <el-input  size="small" v-model="editForm.firstWeight_f" @blur="filterNumber('firstWeight_f')" placeholder="请保留小数点后三位" >
                            <template slot="append">KG</template>
                          </el-input>
                      </el-form-item>

                      <el-form-item label="首重运费"  :rules="{ required: true, message: '该字段必填' }" prop="firstWeightFreight_f" class="form-item-right">
                          <el-input  size="small" v-model="editForm.firstWeightFreight_f" @blur="filterNumber('firstWeightFreight_f')" placeholder="请保留小数点后两位">
                            <template slot="append">USD</template>
                          </el-input>
                      </el-form-item>
                      <span class="title-tip" style="display:inline-block;margin-top:8px;">注:挂号/处理费等请计入首重运费中!</span>   
                      </div>
                      <div>
                      <el-form-item label="续重每增加重量" :rules="{ required: true, message: '该字段必填' }" prop="intervalAddWeight_f">
                          <el-input  size="small" v-model="editForm.intervalAddWeight_f" @blur="filterNumber('intervalAddWeight_f')" placeholder="请保留小数点后三位">
                            <template slot="append">KG</template>
                          </el-input>
                      </el-form-item>

                      <el-form-item label="增加运费"  :rules="{ required: true, message: '该字段必填' }" prop="intervalAddWeightFreight_f" class="form-item-right">
                          <el-input  size="small" v-model="editForm.intervalAddWeightFreight_f" @blur="filterNumber('intervalAddWeightFreight_f')" placeholder="请保留小数点后两位">
                            <template slot="append">USD</template>
                          </el-input>
                      </el-form-item>
                      </div>
                      
                    </div>
                  </el-form>
                 
              </div>
             
              <div class="submit">
                <el-button size="small" type="primary" @click="editSubmit"> <span>保存</span> </el-button>
                <el-button size="small" type="default" @click="doAction('cancel')"> <span>取消</span> </el-button>
              </div>
            </div>
            
          </div>
        </div>
             
      </div>
       <!-- 运费模板组合列表页面 -->
      <div class="setting-show" v-if="type=='show'">
        <div class="info-setting ">
          <div class="logistic ">
            <div class="title">物流方式</div>
            <div class="list">
              <el-checkbox-group v-model="addForm.checkList" size="small"  >
                <div :class="'item '+(item.selected?'select':'')" v-for="item in logisticsList" :key="item.logisticsCode" >
                  <el-checkbox  :label="item.logisticsCode" @change="handleCheckedLogisticsChange" > &nbsp;</el-checkbox> 
                  <div class="name-box" @click="selectLogistics(item.logisticsCode)">
                    <div class="cn">{{item.logisticsName}}{{item.logisticsCode==2400?'(尼日利亚流向必选)':''}}</div>
                  </div>  
                </div>
              </el-checkbox-group>
            </div>
          </div>
          <div class="setting" :style="{'min-height':logisticsList.length*35+'px'}"  >
            <div class="setting-wrap">
              <div class="title" >
                <span>设置分区及价格</span>
                 
                <!-- <a href="http://s1.pic.intmall.net/file/vendors/freight-guide-seko.pdf" v-if="editForm.logisticsTypeCode==2400">SEKO尼日尼亚发货指导</a> -->
                <div class="add-button"><el-button type="success" size="small" @click="addTemplate">新增分区及价格</el-button></div>
              </div>
              <div class="list-box clear-fix" v-loading="setting_show">
                <!-- <div class="item" v-for="(item,index)  in  templateList[editForm.logisticsTypeCode]" :key="index" >
                  <div class="title">组合{{index+1}}</div>
                  <div class="country">
                    <div class="name"><span class="dot-danger"></span>发货地区</div>
                    <div class="detail">
                      <span class="area" v-for="(item2,index2) in item.areas" :key="index2">
                        {{item2.areaName}} 
                        <span v-if="index2<(item.areas.length-1)">,</span>
                      </span>
                    
                    </div>
                  </div>
                  <div class="type">
                    <div class="name"><span class="dot-primary"></span>运费类型设置</div>
                    <div class="detail">{{item.freightType==1?'免运费':'自定义运费'}}</div>
                  </div>
                  <div class="action">
                    <span class="do-action" @click="doAction('edit',index)">编辑</span>
                    <span class="do-action" @click="doAction('delete',index)">删除</span>
                  </div>
                </div> -->
                <div class="item"  v-for="(item,index)  in  templateList[editForm.logisticsTypeCode]" :key="index">
                    <div class="item-box" >
                      <div class="item-title">{{item.itemName}}</div>
                      <div class="action">
                        <span class="do-action" @click="doAction('edit',index)">编辑</span>
                        <span class="do-action" @click="doAction('delete',index)">删除</span>
                      </div>
                      <div class="area-list">
                        <div class="area-title"><span class="dot-primary"></span>区域列表</div>
                        <div class="area-item-box" >
                          <div class="area-item"  v-for="(item2,index2) in item.areas" :key="index2">
                              <div class="area-label">{{item2.areaName}}:</div>
                              <span v-for="(item3,index3) in item2.children" :key="index3">
                                {{item3.areaName}}   
                              </span>
                          </div>
                        </div>
                          
                         
                      </div>
                      <div class="type">
                        <div class="name"><span class="dot-danger"></span>{{item.freightType==1?'免运费':'自定义运费'}}</div>
                        <div class="table-weight" v-if="item.freightType==2">
                          <div>
                            <span class="td-1">首重</span>
                            <span class="td-2">{{item.firstWeight/1000}}kg</span><span class="td-3">{{item.firstWeightFreight/100}}USD</span></div>
                          <div>
                            <span class="td-1">续重</span>
                            <span class="td-2">{{item.intervalAddWeight/1000}}kg</span><span class="td-3">{{item.intervalAddWeightFreight/100}}USD</span>
                          </div>
                        </div>
                      </div>
                    </div>
                </div>
                <!-- <div class="item item-add" v-if="editForm.logisticsTypeCode!=''" >
                    <span @click="addTemplate"><i class="iconfont icon-add2"></i>添加新运费组合</span>
                </div> -->
                <!-- v-if="editForm.logisticsTypeCode!=''&&templateList[editForm.logisticsTypeCode].length==0" -->
                <div class="item-no" v-if="editForm.logisticsTypeCode!=''&&(!templateList.hasOwnProperty(editForm.logisticsTypeCode)||templateList[editForm.logisticsTypeCode].length==0)">
                    暂无分区及价格
                </div>
                <div class="zuhe-tip" v-if="editForm.logisticsTypeCode==''">
                    <div class="tip-title"><span style="color:#f5222d;">注意：</span></div>
                    <p >运费模板中必须包含这五种物流方式中其中一种：<span style="color:#1D8FE5;">顺丰小包挂号、顺丰小包平邮、顺丰经济小包挂号、顺丰经济小包平邮、顺丰特惠，</span>且区域中“俄罗斯”为必选，包含“俄罗斯”的分区组合可编辑不可删除！</p>
                    <p >非必选物流方式无指定必选区域，分区组合可编辑或删除！</p>
                </div>
              </div>
            </div>
            
          </div>
        </div>
        <div class="submit">
            <el-button type="primary" @click="doSubmit" :disabled="supplierId==0"> <span>保存运费模板</span></el-button>
        </div>    
      </div>    
    </div>

  </div>
</template>


<script>
import schema from'async-validator'
export default {
  data(){

    return {
      //规则容器
      myFilter:{
        //顺丰小包、特惠、平邮必须勾选东欧地区 
        // logistics:[16, 17 ,12000],
        logistics:[13,15,16, 17 ,12000]
      },
      //是否为默认模板  supplierId=0为默认模板
      supplierId:'',
      freightId:'',
      setting_show:false,
      //用户信息
      userInfo:{},
      //仓库列表
      warehouseList:[],
      type:'show',
      //物流方式
      logisticsList:[],
      logisticsList_add:[],
      areaList:[],
      //添加表单
     checckedLogistic:[],
      addForm:{
        checkList: [],
        template: {
            id: '',
            supplierId:'',
            templateTitle: '',
            logisticsWarehouseCode: ''
        },
        items: []   
      },
      editForm:{
  
      },
      //运费模板数组
      templateList:{},
      // 展示运费模板
      // templateListShow:[],
      errors:{
        templateTitle:false,
        country:false
      },
      //全选
      checkAll:{
        checkAll:false,
        isIndeterminate:false,
        areaList:[],//全选
      },
      //时效
      prescriptionList:[]
    }
  },
  computed:{
      logisticsName(){
        for(let i in this.logisticsList){         
          if(this.logisticsList[i].logisticsCode==this.editForm.logisticsTypeCode){         
            return this.logisticsList[i].logisticsName
          }
        }
      }
  },
  methods:{
    initEditForm(){  
      let params={
        areaShowFlag:true,
        show:false,
        itemName:'',
        id:"",
        logisticsWarehouseCode:'',
        areaSelectedId:[],
        areaSelectedItems:[],
        freightType:1, 
        logisticsTypeCode: '',
        logisticsTypeCodeArr:[],
        firstWeight: "",
        firstWeight_f: "",
        firstWeightFreight: "",
        firstWeightFreight_f:"",
        intervalAddWeightFreight: '',
        intervalAddWeightFreight_f: '',
        intervalAddWeight:'',
        intervalAddWeight_f:'',
        handlingFee:'',
        discountRate:'',
        areas:[]
      }   
      this.editForm=JSON.parse(JSON.stringify(params));
    },
    initAddForm(){
      this.type='show';
      this.templateList={};
      this.addForm={
        checkList: [],
        template: {
            id: '',
            supplierId:'',
            templateTitle: '',
            logisticsWarehouseCode: ''
        },
        items: []   
      }
    },
    //获取用户信息
    getUserInfo(){
      this.axios.post('/proxy/auth/api/auth/loginUserInfo',{body: { token: this.$cookie.get("token") }}).then((res)=>{
        let data=res.data;
        if(data.head.code=='10000'){
          this.userInfo=data.body;
          this.addForm.template.supplierId=this.userInfo.institutionId;
          this.getWarehouse();
        }
      })
    },
    //获取商家仓库地址
    getWarehouse(){
      this.axios.get('/proxy/fv/supplier/pickup_point.php?act=list_table&page=1&limit=1000',{body: { }}).then((res)=>{
        let data=res.data;
         console.log(typeof data)
        if(data.code=='0'){
          this.warehouseList=data.data;
        }
      })
    },
    // 获取物流方式
    getLogistics() {    
      this.axios.post("/proxy/logistics/logistics/queryType", {body: { languageCode: "cn",status:0 }}).then(response => {
        let data = response.data;
        if (data.head.code == "10000") {
          let logistic_en = data.body.logisticsTypes;
          this.logisticsList=logistic_en.map((item)=>{
            let _item=item;
            _item.selected=false;   
            return _item;
          })
          this.logisticsList_add=JSON.parse(JSON.stringify(this.logisticsList));
          if( this.addForm.template.id !=''){//编辑
            this.getTemplateDetail();
          }
          
        }
      });
    },
    //选择物流方式
    selectLogistics(id){  
      this.setting_show=true;
      this.initEditForm();
      this.editForm.logisticsTypeCode=id;
      this.editForm.logisticsTypeCodeArr=[id];      
      for(let i in this.logisticsList){
        if(this.logisticsList[i].logisticsCode==id){
          this.logisticsList[i].selected=true;
        }else{
          this.logisticsList[i].selected=false;
        }
      }
      setTimeout(()=>{
        this.setting_show=false;         
      },500) 
    },
    //选择物流方式
    handleCheckedLogisticsChange(val,e){
      if(val){
        this.selectLogistics(e.target.defaultValue);
      }
    },
    //获取地区国家
    getAreaList(){
      this.axios.post('/proxy/freight/api/area/list',{body:{}}).then((res)=>{
        let data=res.data;
        if(data.head.code=='10000'){
          this.checkAll.areaList=[];
          let areas=data.body.areas;
          for(let i in areas){
            let item=areas[i];
            for(let j in item.children){
              this.checkAll.areaList.push(item.children[j].id);
              item.children[j].disabled=false;
            }
          } 
          this.areaList=areas;      
        }

      })
    },
    //保存区域到物流方式的组合里
    editSubmit(){
      let _this=this;
      if(this.editForm.areaSelectedId.length==0){
        this.$message({
          type:'error',
          message:'请选择好区域再保存'
        })
        return false;
      }
      if(this.editForm.itemName.trim()==''){
        this.$message({
          type:'error',
          message:'请输入分区及价格名称'
        })
        return false;
      }
      if(this.editForm.freightType==2){//自定义运费  需要验证
        this.$refs.weightForm.validate((validate)=>{
          if(validate){
            saveDate();  
          }
        })
      }else{//]免运费不需要验证
        saveDate();
      }  
      //存数据到组合数组里
      function saveDate(){   
        _this.editForm.areas=_this.editForm.areaSelectedItems;
        let flag=false;
        for(let i in _this.templateList[_this.editForm.logisticsTypeCode]){
            let item=_this.templateList[_this.editForm.logisticsTypeCode][i];
            if((item.id !=''&&item.id==_this.editForm.id)||(item.id ==''&&item.tmpId&&item.tmpId==_this.editForm.tmpId)){      
              _this.templateList[_this.editForm.logisticsTypeCode][i]=JSON.parse(JSON.stringify(_this.editForm));
              flag=true;
              break;                              
            } 
        }  
        if(!_this.templateList[_this.editForm.logisticsTypeCode]){
          _this.templateList[_this.editForm.logisticsTypeCode]=[];
        }
        if(!flag){
          _this.editForm.tmpId=_this.templateList[_this.editForm.logisticsTypeCode].length+1;
          _this.templateList[_this.editForm.logisticsTypeCode].push(JSON.parse(JSON.stringify(_this.editForm)));
        }        
        _this.type='show';
        _this.selectLogistics(_this.editForm.logisticsTypeCode);
       
      }
      this.filterLogistics();
    },
    // 筛选已经选择的物流方式   有组合的物流方式才会被勾选
    filterLogistics(){
      let checkList=[];    
      let list= JSON.parse(JSON.stringify(this.templateList));
      for(let i in list){
        if(list[i].length>0){
          checkList.push(Number(i));
        }
      }   
      this.addForm.checkList=[...new Set(checkList)];  
    },
    //添加运费模板
    addTemplate(){
      if(this.editForm.logisticsTypeCode==''){
        this.$message({
          type:'warning',
          message:'请先选择物流方式再新增分区及价格!'
        });
      }else{
        this.filterArea('add',-1);       
        this.type='add';
      }
    },
    //同一个物流方式的地区不能重复选择
    filterArea(type,index){
      this.editForm.areaSelectedId=[];
      let logisticsTypeCode= this.editForm.logisticsTypeCode;
      let list=this.templateList[logisticsTypeCode];//当前物流方式的所有组合
      let areaIds=[];//所有的已经勾选的区域id
      let areaIds_other=[];//所有的已经勾选的区域id(不包括本组合)
      for(let i in list){
        let areas=list[i].areas;
        for(let j in areas){
          for(let k in areas[j].children){
            areaIds.push(areas[j].children[k].id);  
            if(i!=index){
              areaIds_other.push(areas[j].children[k].id);    
            }
          }
   
        }       
      }
      //勾选已经选的区域
      if(type=='edit'){
        let item=this.templateList[logisticsTypeCode][index];//编辑组合（指定物流方式与组合索引）
        // this.editForm.areaSelectedId=JSON.parse(JSON.stringify(item)).areas.map(_item=>{
        //   return _item.id;
        // })
        let areaSelectedId=[];
        for(let i in item.areas){
          for(let j in item.areas[i].children){
            areaSelectedId.push(item.areas[i].children[j].id)
          }
        }
        this.editForm.areaSelectedId=areaSelectedId;
      }
      //顺丰小包、特惠、平邮必须勾选东欧地区 
      if(this.myFilter.logistics.includes(parseInt(logisticsTypeCode))){
          if(!(areaIds.includes(4)||areaIds.includes('26'))){
            this.editForm.areaSelectedId.push('26');
          }
      }
      //禁止组合区域重复
      for(let i in this.areaList){
        let area=this.areaList[i];
        for(let j in area.children){
          let child=area.children[j];
          child.disabled=areaIds_other.includes(child.id)||areaIds_other.includes(parseInt(child.id));
          if(this.myFilter.logistics.includes(parseInt(logisticsTypeCode)) && child.id=='26'){//如果是顺丰小包 东欧disabled
              child.disabled=true;
          }
        }

      }
      this.checkAll.areaList=[];
      let areas=this.areaList;    
      for(let i in areas){
        let item=areas[i];
        for(let j in item.children){
          if(logisticsTypeCode==1306){
              if(item.id==60){
                this.checkAll.areaList.push(item.children[j].id);
              }
          }else{
              if(item.id!=60){
                this.checkAll.areaList.push(item.children[j].id);
              }         
          }           
        }
      } 
      console.log(this.checkAll.areaList)
      //触发全选样式
      this.handleCheckedAreasChange(this.editForm.areaSelectedId);
    },
    //组合的删除修改操作

    doAction(type,data,tmpId){
        switch(type){
          //组合删除
          case 'delete':  
            let logisticsTypeCode= this.editForm.logisticsTypeCode;  
            //顺丰小包、特惠、平邮必须包含东欧地区不能删除      
            if(this.myFilter.logistics.includes(parseInt(logisticsTypeCode))){
              let areasId=[];
              let areas= this.templateList[logisticsTypeCode][data].areas;  
              for(let i in areas){
                for(let j in areas[i].children){
                  areasId.push( areas[i].children[j].id)
                }
              }         
              console.log(areasId)
              if(areasId.includes('26')){
                this.$message({
                  type:'error',
                  message:'顺丰物流包含“俄罗斯”的分区不可删除，可进行编辑或新建组合！'
                })
                return false;
              }
            }   
            this.$confirm('确定删除该组合?', '警告', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {              
              this.templateList[logisticsTypeCode].splice(data,1);
              this.templateList=JSON.parse(JSON.stringify(this.templateList));
              this.filterLogistics();
             
            }).catch(() => {
              this.$message({
                type: 'info',
                message: '已取消删除'
              });          
            });
          
         
          break;
          //组合编辑
          case 'edit':                              
            let item =JSON.parse(JSON.stringify(this.templateList[this.editForm.logisticsTypeCode][data]));
            item.logisticsTypeCodeArr=[item.logisticsTypeCode]
            this.initEditForm();     
            Object.assign(this.editForm,item);
            this.filterArea('edit',data);           
            this.type='add';  
          break;
          //取消保存
          case 'cancel':
            this.type='show';
          break;
        }
    },
    
    //获取运费模板详情
    getTemplateDetail(){
      this.axios.post('/proxy/freight/api/template/detail',{body:{id:this.addForm.template.id}}).then((res)=>{
          let  data=res.data;      
          if(data.head.code=='10000'){
            data.body.checkList=[];
            let tmp={};    
            for(let i in data.body.items){   
                data.body.checkList.push(data.body.items[i].logisticsTypeCode);
                let item=JSON.parse(JSON.stringify(data.body.items[i]));
                item.firstWeight_f=item.firstWeight/1000;            
                item.firstWeightFreight_f=item.firstWeightFreight/100;      
                item.intervalAddWeight_f=item.intervalAddWeight/1000; 
                item.intervalAddWeightFreight_f=item.intervalAddWeightFreight/100;    
                if(tmp[item.logisticsTypeCode]){
                  tmp[item.logisticsTypeCode].push(item);
                }else{
                  tmp[item.logisticsTypeCode]=[item]
                }
            } 
            this.templateList=JSON.parse(JSON.stringify(tmp))
            this.addForm=JSON.parse(JSON.stringify(data.body));
            this.addForm.checkList=[...new Set(this.addForm.checkList)];
            if(this.addForm.checkList.length>0){
              for(let i in this.logisticsList){                
                for(let j in this.addForm.checkList){
                  if(this.logisticsList[i].logisticsCode==this.addForm.checkList[j]){
                      this.editForm.logisticsTypeCode=this.logisticsList[i].logisticsCode;
                      this.editForm.logisticsTypeCodeArr=[JSON.parse(JSON.stringify( this.editForm)).logisticsTypeCode].concat([]);
                      this.selectLogistics(this.editForm.logisticsTypeCode);
                      return false;
                      break;
                  }
                }
              }             
            }
                      
          }
      })
    },

    filterNumber(field){
      switch(field){
        case "firstWeight_f":
            this.editForm.firstWeight_f=isNaN(parseFloat(this.editForm.firstWeight_f))?"":Number(parseFloat(this.editForm.firstWeight_f).toFixed(3));
            this.editForm.firstWeight_f=this.editForm.firstWeight_f==''?'':Math.abs(this.editForm.firstWeight_f);
            this.editForm.firstWeight=this.editForm.firstWeight_f?this.editForm.firstWeight_f*1000:"";
        break;
        case "firstWeightFreight_f":
            this.editForm.firstWeightFreight_f=isNaN(parseFloat(this.editForm.firstWeightFreight_f))?"":Number(parseFloat(this.editForm.firstWeightFreight_f).toFixed(2));
            this.editForm.firstWeightFreight_f=this.editForm.firstWeightFreight_f==''?'':Math.abs(this.editForm.firstWeightFreight_f);
            this.editForm.firstWeightFreight=this.editForm.firstWeightFreight_f?this.editForm.firstWeightFreight_f*100:'';
        break;
        case "intervalAddWeight_f":
            this.editForm.intervalAddWeight_f=isNaN(parseFloat(this.editForm.intervalAddWeight_f))?"":Number(parseFloat(this.editForm.intervalAddWeight_f).toFixed(3));
            this.editForm.intervalAddWeight_f=this.editForm.intervalAddWeight_f==''?'':Math.abs(this.editForm.intervalAddWeight_f);
            this.editForm.intervalAddWeight=this.editForm.intervalAddWeight_f?this.editForm.intervalAddWeight_f*1000:'';
        break;
        case "intervalAddWeightFreight_f":
            this.editForm.intervalAddWeightFreight_f=isNaN(parseFloat(this.editForm.intervalAddWeightFreight_f))?"":Number(parseFloat(this.editForm.intervalAddWeightFreight_f).toFixed(2));
            this.editForm.intervalAddWeightFreight_f=this.editForm.intervalAddWeightFreight_f==''?'':Math.abs(this.editForm.intervalAddWeightFreight_f);
           this.editForm.intervalAddWeightFreight=this.editForm.intervalAddWeightFreight_f?this.editForm.intervalAddWeightFreight_f*100:"";
        break;
      } 
    },
    
    //区域全选
    handleCheckAllChange(val,event) {
      let logisticsTypeCode= this.editForm.logisticsTypeCode;
      if(this.myFilter.logistics.includes(parseInt(logisticsTypeCode))){//顺丰物流
        let included=this.editForm.areaSelectedId.includes('26')||this.editForm.areaSelectedId.includes(4);
        if(event.target.checked){
          let tmp=[];
          for(let i in this.areaList){
            let item=this.areaList[i];
            for(let j in item.children){
              if(!item.children[j].disabled){
                if(this.checkAll.areaList.includes(item.children[j].id)){
                    tmp.push(item.children[j].id)
                }
               
              }
            }
          }
          this.editForm.areaSelectedId=tmp; 
        }else{
          this.editForm.areaSelectedId = []; 
        }
        if(included){
          this.editForm.areaSelectedId.push('26');
        }
      }else{
        if(event.target.checked){
          let tmp=[];
          for(let i in this.areaList){
            let item=this.areaList[i];
            for(let j in item.children){
              if(!item.children[j].disabled){
                if(this.checkAll.areaList.includes(item.children[j].id)){
                    tmp.push(item.children[j].id)
                }
              }
            }
          }
          this.editForm.areaSelectedId=tmp; 
        }else{
          this.editForm.areaSelectedId = []; 
        }
      }
      this.checkAll.isIndeterminate = false;
    },
    handleCheckedAreasChange(value) {
      let checkedCount = value.length; 
      this.checkAll.checkAll = checkedCount === this.checkAll.areaList.length;
      this.checkAll.isIndeterminate = checkedCount > 0 && checkedCount < this.checkAll.areaList.length;
    },
    //获取物流方式区域时效
    getPrescription(){
      this.axios('/proxy/freight/api/logisticsareaperiod/getall',{body:{}}).then(res=>{
        let data=res.data;
        if(data.head.code=='10000'){
          this.prescriptionList=data.body.list;
        }
      })
    },
    //计算区域时效
    computedPrescription(areaId){   
      let str="";
      for(let i in this.prescriptionList){      
        let item=this.prescriptionList[i];
        if(item.freightAreaId==areaId&&item.logisticsCode==this.editForm.logisticsTypeCode){
          str=item.minDays+'~'+item.maxDays+'工作日'
        }
      }
      if(str==''){
        str='暂无参考时效';
      }
      return str;
    },
    // 通过物流code获取物流对象
    getLogisticById(id){
      for(let i in this.logisticsList){
        if(this.logisticsList[i].logisticsCode==id){
          return this.logisticsList[i];
          break;
        }
      }
    },
    //当前选择的物流方式
    watchCheckList(){
      let tmp="";
      for(let i in this.logisticsList){
        let item=this.logisticsList[i];
        if(item.selected){
          tmp=item.logisticsName;
          break;
        }  
      }
      return tmp
    },
    //初始化数据
    init(){
      this.initAddForm();
      this.supplierId=this.$route.query.supplierId?this.$route.query.supplierId:'add'; 
      this.addForm.template.id=(this.$route.params.id=='add'?'':this.$route.params.id);
      // this.getPrescription();
      this.initEditForm();
      this.getUserInfo();
      this.getLogistics();
      this.getAreaList();  
      console.log(this.addForm)
    },
    //提交运费模板表单
    doSubmit(){
      let params={template:{},items:[]};    
      let templateList=Object.values(this.templateList);       
      for(let i in templateList){
        for(let j in templateList[i]){
          let _obj=JSON.parse(JSON.stringify(templateList[i][j]));
          if(this.addForm.checkList.includes(_obj.logisticsTypeCode)||
             this.addForm.checkList.includes(parseInt(_obj.logisticsTypeCode))
            ){
              params.items.push(_obj);
          }          
        }
      } 
      
      params.template=JSON.parse(JSON.stringify(this.addForm.template));    
      //验证表单
      if(!params.template.templateTitle){
        this.errors.templateTitle=true;      
        this.$refs.ref_templateTitle.focus();
        return false;
      }else{
         this.errors.templateTitle=false;
      }  
      if(!params.template.logisticsWarehouseCode){
        this.errors.country=true;      
        this.$refs.ref_country.focus();
        return false;
      }else{
         this.errors.country=false;
      }  
      //所有物流方式必须设置分区及价格方可保存，如没有设置完整则提示：请对已选择物流方式进行运费组合设置  
      let lengthFlag=true; 
      //勾选物流方式而没有添加组合  
      for(let i in this.addForm.checkList){    
        if(!(this.templateList.hasOwnProperty(this.addForm.checkList[i]))||this.templateList[this.addForm.checkList[i]].length==0){
          let logistics=this.getLogisticById(this.addForm.checkList[i]);
          this.$message({
            type:'error',
            message:`请对${logistics.logisticsName}物流方式进行运费类型设置！`
          })
          return false;          
          break;
        }
      } 
      //没有勾选默认顺丰物流  
      let tmpArr=this.addForm.checkList.filter(item=>{
          return this.myFilter.logistics.includes(parseInt(item));
      });
      if([...new Set(tmpArr)].length==0){
        this.$message({
          // customClass:"mgs-freight",
          // duration:300000,
          type:'error',
          message:`顺丰小包挂号、顺丰小包平邮、顺丰经济小包挂号、顺丰经济小包平邮、 顺丰特惠必选其中一种物流！`
        })
        return false;            
      }
      // return false;
      if(params.items.length==0){
        this.$message({
          type:'error',
          message:'请对已选择物流方式进行运费组合设置！'
        })
        return false;
      }  
      //整理areas 报文格式
      for(let i in params.items){
        let item=params.items[i];
        let areas=JSON.parse(JSON.stringify(item)).areas;
        let tempArea=[];
        for(let j in areas){
          for(let k in areas[j].children){
            tempArea.push(areas[j].children[k]);
          }
        }
        item.areas=tempArea;
      }
      
      let url='api/template/edit';
      if(this.addForm.template.id==''){
        url='api/template/create';
      }
      this.axios.post('/proxy/freight/'+url,{body:params}).then((res)=>{
        let data=res.data;
        if(data.head.code=='10000'){
          this.$message({
            type:'success',
            message:'保存成功'
          });
          setTimeout(()=>{
            this.$router.push('/vendor/freight-tmp');
          })
        }
      })
    },
  },
  
  created(){
   this.init();
  },
  watch:{
       "editForm.areaSelectedId":function(arr){
         this.editForm.areaSelectedItems=[];
         for(let i in arr){
            for(let j in this.areaList){
              for(let k in this.areaList[j].children){
                if(arr[i]==this.areaList[j].children[k].id){
                  let flag=true;
                  for(let q in this.editForm.areaSelectedItems){
                    if(this.editForm.areaSelectedItems[q].id==this.areaList[j].children[k].parentId){
                      flag=false;
                      this.editForm.areaSelectedItems[q].children.push(this.areaList[j].children[k]);
                      break;
                    }
                  }
                  if(flag){//一级地区不存在
                      let obj=JSON.parse(JSON.stringify(this.areaList[j]));
                      obj.children=[this.areaList[j].children[k]];
                      this.editForm.areaSelectedItems.push(obj);
                  }                
                }
              }        
            }
         }
       },
       
       "$route":function(){
         this.init();
       },
           
  },
  beforeRouteUpdate(to, from, next) {
    let path=['/login','/vendor/freight-tmp']
    if(path.includes(to.path)){
      next()
    }else{
      this.$confirm('即将离开页面，请确定已经保存了运费模板？', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {              
        next();
      }).catch(() => {
                
      });    
    }
  },
  beforeRouteLeave(to, from, next) {
    let path=['/login','/vendor/freight-tmp']
    if(path.includes(to.path)){
      next()
    }else{
      this.$confirm('即将离开页面，请确定已经保存了运费模板？', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {              
        next();
      }).catch(() => {
                
      });    
    }
  }

}
