            a{
              position: absolute;
              right:10px;
              top: 0px;
            }
            .tip-title{
              color: #f5222d;
              margin-left:70px;
              font-weight: bolder;
            }
          }
          
          
        }  
        .weight-tip{
          display: inline-block;
          margin-top: 8px;
          color: #888;
          margin-left: 60px;
        }   
        //添加模板表单
        .sub-title{
          font-size: 14px;
          color:#1D8FE5;
          margin-top: 15px;
          margin-bottom: 10px;
        
          .title-tip{
            color: #f5222d;
            margin-left: 50px;
          }
        }
        .content{
          padding-left: 10px;
          .open-close{
            cursor: pointer;
            .iconfont{
              color: #006EBA;
              font-size: 20px;
              vertical-align: middle;
              margin-right: 4px;
              
            }
          }
          .search{
            background-color: #fff;
            .el-input{
              width: auto;
            }
            margin-bottom: 5px;
            padding-top: 5px;
            
          }
          .check-all{
            padding-bottom: 10px;
          }
         
          .area-group{
            .list-box{
              display: inline-block;
              width: 390px;
              height: 170px;
              box-sizing: border-box;
              padding: 8px;
              position: relative;
              .item-title{
                position: absolute;
                top: -1px;
                left: 35px;
                padding: 0 5px;
                background-color: #fff;
                font-size: 16px;
                font-weight: bold;
              }
              .item{
                height: 140px;
                border:1px solid #ccc;
                box-shadow: 0 0 8px #ccc;
                border-radius: 10px;
                overflow-y:auto;
                box-sizing: border-box;
                padding: 15px 10px;
                padding-top: 25px;
              }
              .el-checkbox{
                margin:0;
                margin-bottom:10px;
                width: 110px;
              }
            }
          }

          .country-selected{
            margin-top: 30px;
            height: 46px;
            line-height: 46px;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
            padding: 0 15px;
            width: 900px;
            border: 1px solid #D8D8D8;
            font-size: 12px;
            span.en{
              font-size: 14px;
            }
          }
          .logistics-time{
            width: 516px;
            .item{
              border-bottom: 1px solid #D8D8D8;
              padding:0;             
              .area-name{
                display: inline-block;
                width: 245px;
                padding-left:18px; 
                border-right: 1px solid #D8D8D8;
                height: 32px;
                line-height: 32px;
              }
              .area-time{
                display: inline-block;
                height: 32px;
                line-height: 32px;
                padding-left:18px; 
              }
            }
          }
          

        }
        .freight-item{
          .item{
            margin-bottom: 15px;
            span{
              margin-right: 4px;
            }
            .item-label{
              margin-left: 10px;
            }
          }
        }
        .tip{
          padding-top: 5px;
          padding-bottom: 20px;
        }
        .submit{
          margin-bottom: 30px;
          margin-top: 8px;
          span{
            display: inline-block;
            padding: 0 15px;
          }
        }
        
        
    }
    .zuhe-tip{
      width: 584px;
      margin: 50px auto;
      box-shadow: 0 0 18px #000;
      padding: 50px;
      border-radius: 10px;
      background: #fff;
      position: relative;
      padding-left: 70px;
      .tip-title{
        position: absolute;
        top: 30px;
        left: 33px;
      }
      p{
        margin-top:0;
        position: relative;
        padding-left: 18px;
        &::before{
          content: "";
          display: inline-block;
          vertical-align: middle;
          width: 6px;
          height: 6px;
          position: absolute;
          top: 7px;
          left:0;
          background-color: #ccc;
        }
        
      }
    }
  }

</style>
<style lang="less">
.setting-add{
  .search .el-icon-search {
    padding-top: 0px;
  }
  .area-list{
    .el-checkbox__label{ 
      font-weight:700;
    }
    .el-checkbox__input.is-checked{
      &+span.el-checkbox__label{
      color:#006EBA;

      }
    }
    
  }
  .el-checkbox__input.is-disabled.is-checked .el-checkbox__inner::after{
    border-color:#000;
  }
}
.el-form--inline .el-form-item{
  margin-bottom:10px;
  margin-right:1px;
}
.el-form-item__content{
  line-height:normal;
}
.el-form--inline .el-form-item__content{
  vertical-align:middle;
}
.el-form-item__error{
  padding-top:0;
  padding-left:0;
}
.el-form--inline .el-form-item__label{
  color:#000;
}
.el-input__inner{
  width: 350px;
}
.freight-item{
  .el-input{
    width: auto;
    .el-input__inner{
      width: 140px;
    }
  }
  .form-item-right{
    label{
      width:80px !important;
    }
  }
}
.logistic{     
  .el-checkbox{
    margin-top:10px;
    margin-left:25px;
  }
  .el-checkbox__input.is-checked .el-checkbox__inner {
      background-color: #fff;
      border-color: #666;
  }
  .el-checkbox__input.is-focus .el-checkbox__inner {
      background-color: #fff;
      border-color: #666;
  }
  .el-checkbox__inner:hover{
    border-color: #666;
  }
  .el-checkbox__inner::after{
    border-color: #222;
  }

}
.el-card{
  background:#fff;
}
.el-card__body{
  padding: 0;
}
.el-card__header{
  padding: 8px 20px;
  
}
.el-input-group__append, .el-input-group__prepend{
  color:#222;
}
.setting-add .el-checkbox__input.is-disabled.is-checked .el-checkbox__inner::after{
  color:#FE0C25;
}
.wrap-sys-main .main-content{
  background-color:#fff !IMPORTANT;
}
 .zuhe-name{
   padding-top: 15px;
   position: relative;
   &::before{
       content: '*';
       color: #ff4949;
       margin-right: 4px;
   }
  
  .el-input--small{
    display: inline-block;
    width: 250px;
    margin-left: 10px;             
  }
  a{
    position: absolute;
    top:20px;
    right: 20px;
    color:#1D8FE5;
  }

}
.el-message{
  left:65%;
}
</style>




